html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

#Home {
  padding: 0 8%;
  padding-top: 20px;
  padding-bottom: 30px; }
  #Home h2 {
    font-size: 48px; }
  #Home .MyImag img {
    display: block;
    width: 100%;
    min-height: auto; }
  #Home .MyInt {
    padding-left: 20px; }
    @media (max-width: 425px) {
      #Home .MyInt {
        padding-left: 10px; } }
    #Home .MyInt h3 {
      border-left: 2px solid #000;
      border-bottom: 2px solid #000;
      padding: 0 0 10px 5px;
      width: 20%;
      font-weight: 900; }
      @media (max-width: 425px) {
        #Home .MyInt h3 {
          width: 70%; } }
    #Home .MyInt p {
      font-size: 23px;
      padding-left: 20px;
      font-weight: 500; }
      @media (max-width: 425px) {
        #Home .MyInt p {
          line-height: 1.5;
          padding-left: 0; } }

#Gallery {
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 58px; }
  #Gallery h2 {
    font-size: 48px;
    color: white; }
  #Gallery .GalleryList {
    padding: 0 8%; }
    #Gallery .GalleryList ul li {
      display: inline-block;
      width: calc(100% / 4 - 40px);
      margin: 0 20px;
      background-color: rgba(0, 0, 0, 0);
      min-height: 350px; }
      #Gallery .GalleryList ul li img {
        display: block;
        width: 100%;
        height: auto; }
