@import "@/assets/scss/all.scss";













































































#Home {
  padding: 0 8%;
  padding-top: 20px;
  padding-bottom: 30px;
  // min-height: 100vh;
  h2 {
    font-size: 48px;
  }
  .MyImag {
    img {
      display: block;
      width: 100%;
      min-height: auto;
    }
  }
  .MyInt {
    padding-left: 20px;
    @include dai_425 {
      padding-left: 10px;
    }
    h3 {
      border-left: 2px solid #000;
      border-bottom: 2px solid #000;
      padding: 0 0 10px 5px;
      width: 20%;
      font-weight: 900;
      @include dai_425 {
        width: 70%;
      }
    }
    p {
      font-size: 23px;
      padding-left: 20px;
      font-weight: 500;
      @include dai_425 {
        line-height: 1.5;
        padding-left: 0;
      }
    }
  }
}
#Gallery {
  width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 58px;
  h2 {
    font-size: 48px;
    color: white;
  }
  .GalleryList {
    padding: 0 8%;
    ul {
      li {
        display: inline-block;
        width: calc(100% / 4 - 40px);
        margin: 0 20px;
        background-color: rgba(0, 0, 0, 0);
        min-height: 350px;
        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }
  }
}
